/*               */
/* css variables */
/*               */
* {
  --sidebarZindex: 6;

  --transparentBackground: rgba(255, 255, 255, 0);
  --translucentBackground: rgba(211, 211, 211, 0.25);

  --boxShadowSettings: 0 4px 8px 0 rgba(0, 0, 0, 0.2),
    0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

/* format card parent */
#contextBtnContainer {
  /* coordinate */
  z-index: var(--sidebarZindex);
  position: absolute; /* super important */
  bottom: 0%;

  /* size */
  width: 50vw;
  height: auto;

  /* offset */
  padding-top: 32px;
  padding-left: 32px;
  padding-right: 32px;
  padding-bottom: 32px;

  /* color */
  /* background-image: linear-gradient(rgba(255, 255, 255, 0.25), rgba(255, 255, 255, 1.0)); */

  /* text formatting */
  display: block;
  text-align: left;
}

.contextBtn {
  /* coordinate */
  float: left;
  position: relative;
  height: 1.5rem;
  width: 1.5rem;

  text-align: center;
  justify-content: center;

  font-size: 20px;
  cursor: pointer;
  /* background-color: var(--transparentBackground); */
  background-color: black;
  border-radius: 1rem;
  color: white;
  border: none;
}
