* {
  --cardZindex: 5;

  --transparentBackground: rgba(255, 255, 255, 0);
  --translucentBackground: rgba(211, 211, 211, 0.25);

  --boxShadowSettings: 0 4px 8px 0 rgba(0, 0, 0, 0.2),
    0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

/* format card parent */
.cardContainer {
  /* coordinate */
  z-index: var(--cardZindex);
  position: absolute; /* super important */

  /* size */
  width: 100vw;
  height: 100vh;

  /* offset */
  padding-top: 64px;
  padding-left: 32px;
  padding-right: 32px;
  padding-bottom: 64px;

  /* text formatting */
  display: block;
  text-align: left;

  /* color/styling */
  background-image: linear-gradient(
    rgba(255, 255, 255, 0.75),
    rgba(255, 255, 255, 0.15)
  );
  -webkit-backdrop-filter: blur(3px);
          backdrop-filter: blur(3px);
}

/* format card children (contents) */
.textBlock {
  /* display controls whether each item
